  /* 动画 */
.fade-enter-active, .fade-leave-active {
    -webkit-transition: opacity 5s;
    transition: opacity 5s;
}
.fade-enter, .fade-leave-to {
    opacity: 0;
}
  /* select及其下方的接口宽度样式 */
.swagger-left {
    width: 21%;
    margin-top: 0px;
    position: fixed;
    height: 100%;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}
  /* 单选框 */
.form-control {
    display: block;
    width: 86%;
    margin: 0 auto 5px;
    padding: 4px 6px;
    border: 0;
    border-bottom: 1px solid #555;
    outline: none;
    height: 50px;
}
.nav-list {
    margin: 0;
    padding: 0;
}
.nav-list > li {
    display: block;
    margin: 0 auto 5px;
    border: 0;
    position: relative;
    border-left: 5px solid #fff;
    cursor: pointer;
    text-align: left;
    padding: 16px 0 16px 5%;
}
.nav-list > li:hover, .nav-list > li.active {
    background-color: #F3F8E4;
    color: #8ABF00;
    border-left: 5px solid #8ABF00;
}
  /* 第一层接口列表名字 */
.navList-name {
    margin-right: 14px;
    float: left;
}
.navList-description {

    width: 172px;
    display: inline-block;
    text-align: left;
}
.navList-number {
    position: absolute;
    right: 27px;
    top: 16px;
    width: 20px;
    height: 20px;
    background-color: #FF3C43;
    border-radius: 10px;
    text-align: center;
    line-height: 20px;
    color: #fff;
    font-size: 14px;
}
  /* 接口列表 */
.swagger-category {
    padding: 0;
    margin-left: 21%;
    width: 22%;
    margin-top: 0px;
    position: fixed;
    /* background: #337ab7; */
    height: 100%;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}
.swagger-category .categoryLi {
    text-align: left;
    margin-bottom: 5px;
    padding: 5px 10px;
}
.categoryLi .categoryLi-type {
    height: 20px;
    line-height: 20px;
}
.categoryLi .categoryLi-path {
    padding: 2px 4px;
    font-size: 90%;
    color: #c7254e;
    white-space: nowrap;
    background-color: #f9f2f4;
    border-radius: 4px;
}
.categoryLi .categoryLi-name {
    display: block;
    height: 20px;
    line-height: 20px;
}
.notify .notify-msg {
    height: auto;
    max-width: 260px;
}
  /*  tab切换选项 */
.tabSwitch{
    margin-left: 43%;
    margin-right: 15px;
    padding-left: 10px;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}
.tabSwitch ul{
    font-size: 0;
    white-space: nowrap;
    overflow-y: hidden;
    overflow-x: scroll;
    text-align: left;
}
  /* 滚动条样式 */
.tabSwitch ul::-webkit-scrollbar {/*滚动条整体样式*/
    width: 5px;     /*高宽分别对应横竖滚动条的尺寸*/
    height: 8px;
}
.tabSwitch ul::-webkit-scrollbar-thumb {/*滚动条里面小方块*/
    border-radius: 5px;
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    background: #EBEBEB;
}
.tabSwitch ul::-webkit-scrollbar-track {/*滚动条里面轨道*/
    -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
    border-radius: 10px;
    background: #FFF;
}
.tabSwitch li{
    padding: 10px 6px 10px 15px;
    font-size: 14px;font-weight: 500;
    border-top: 1px solid #dbdbdb;
    cursor: pointer;display: inline-block;
    float: none;
    -webkit-box-shadow: 1px 1px 2px #e9e4e4;
            box-shadow: 1px 1px 2px #e9e4e4;
}
.tabSwitch li.active{
    background-color: #89BF05;color:#fff;
}
.tabSwitch li span{
    padding-right: 9px;
}
.tabSwitch li a{
    text-decoration: none;color:black;
}
.tabSwitch li.active span,
  .tabSwitch li.active a{
    color:#fff;
}
.tabSwitch li a:hover,
  .tabSwitch li.active a:hover{
    color:rgb(235, 91, 91);
}
  /* 响应式 */
@media screen and (min-width: 1600px){
.swagger-left{
      width: 18%;
}
.swagger-category{
      width: 18%;margin-left: 18%;
}
.tabSwitch{
      margin-left: 36%;
}
}





























































































































































































































































































































































































































































































































































